@import "../utils/vars";

@mixin onTablet {
    @media (min-width: 640px) {
        @content;
    }
}

@mixin onSmallDesktop {
    @media (min-width: 1024px) {
        @content;
    }
}

@mixin onDesktop {
    @media (min-width: 1600px) {
        @content;
    }
}

@mixin top-action {
    padding-top: 20px;

    @include onSmallDesktop {
        padding-top: 48px;
    }
}

@mixin font-p-600 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
 color: $color-w;
}

@mixin font-p-700 {
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    color: $color-w;
}

@mixin font-o-400 {
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    color: $color-w;
}

@mixin icon {
    width: 18px;
    height: 8px;
}

@mixin slider {
    width: 32px;
        height: 32px;
}

@mixin top-action {
    padding-top: 20px;

    @include onSmallDesktop {
        padding-top: 48px;
    }
}

@mixin big-place {
    width: 186px;
    height: 172px;
    margin: 40px 0 60px;
}

@mixin small-place {
    width: 146px;
    height: 134px;
    margin: 30px 0 50px;
}

@mixin big-decor {
    width: 56px;
    height: 140px;
}

@mixin small-decor {
    width: 44px;
    height: 110px;
}

@mixin testimonials-big-photo {
    width: 140px;
    top: 16px;
    left: 23px;
}

@mixin testimonials-small-photo {
    width: 110px;
    top: 12px;
    left: 18px;
}